.hero.section {
    background-image: linear-gradient(rgba(52, 52, 52, 0.6), rgba(52, 52, 52, 0.6)),
        url("../images/hero/hero-background.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 955px;
    padding-top: 0px;
}

.hero {
    div {
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            font-size: 72px;
            line-height: 1.16;
            color: $white;
            max-width: 660px;
            margin-bottom: 16px;
        }

        p {
            font-size: 18px;
            line-height: 1.66;
            color: $white;
            max-width: 640px;
            margin-bottom: 35px;
        }

        div {
            display: flex;
            flex-direction: row;
            justify-content: center;
            gap: 28px;
            height: 75px;
            width: 100%;

            button.secondary-button {
                color: $white;
                border-color: $white;

                &:hover {
                    color: $hover-orange;
                    border-color: $hover-orange;
                }

                &:focus {
                    color: $focus-orange;
                    border-color: $focus-orange;
                }
            }
        }
    }
}